  justify-content: space-between;
  height: 18px;
  font-size: 12px;
  font-weight: normal; }

.publication-footer__views-comments {
  display: flex;
  align-items: center;
  overflow: hidden;
  white-space: nowrap;
  opacity: 0.9; }

.publication-footer__views-icon {
  display: none; }

.publication-footer__views {
  margin-left: 6px; }

.publication-footer__comments {
  margin-left: 16px;
  color: #303131; }

.publication-footer__likes {
  display: flex;
  align-items: center; }

.publication-footer__likes-count {
  display: none;
  transition: all 0.3s ease; }
  .publication-footer__likes-count.like {
    display: block;
    padding-right: 5px; }

.publication-footer__like-btn {
  font-size: 0;
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer; }

.publication-footer__svg {
  fill: transparent;
  stroke: #e84a43;
  stroke-width: 1.5px;
  transition: fill 0.3s ease; }
  .publication-footer__svg.like {
    fill: #e84a43; }

@media (max-width: 415px) {
