      font-size: 0.88rem; }
    .search__input-block {
      margin-right: 0; }
  .input-block__dandruff {
    fill: #fff; }
  .input-block__input.open {
    color: #fff; }
  .input-block__underline.open {
    background-color: #fff; }
  .input-block__cross {
    fill: #fff; }
  .post {
    padding: 0; }
    .post__wrapper {
      display: initial;
      height: auto;
      width: 100%; }
    .post__img-block {
      width: 100%;
      height: auto; }
    .post__img {
